.IconStack > .Icon {
  position: absolute;
  width: 100%;
  text-align: center;
}


.IconStack {
  position: relative;
  display: inline-block;
  height: 1.2em;
  line-height: 2em;
  vertical-align: middle;
}

.IconStack:after {
  color: transparent;
  content: '.';
}
